<template>
  <v-container>
    <h1>Projects</h1>
    <v-btn @click="logout()">Log out</v-btn>

    <v-divider></v-divider>
    <v-list two-line>
      <v-list-tile avatar ripple  @click="$router.push({name: 'ProjectCreate'})" color="green darken-1">
        <v-list-tile-content>
          <v-list-tile-title>Add New</v-list-tile-title>
        </v-list-tile-content>
        <v-list-tile-action>
          <v-icon color="green darken-1">add_circle</v-icon>
        </v-list-tile-action>
      </v-list-tile>
      <v-divider></v-divider>
      <v-divider></v-divider>
      <template v-for="project in projects">
        <v-list-tile avatar ripple :key="project.id" @click="">
          <v-list-tile-avatar>
            <v-icon v-if="project.publish" color="blue darken-1" >visibility</v-icon>
            <v-icon v-else color=" grey darken-1" >visibility_off</v-icon>
          </v-list-tile-avatar>
          <v-list-tile-content>
            <v-list-tile-title>
              {{ project.title }} (<i><strong>{{project.timestamp}}</strong></i>)
            </v-list-tile-title>
            <v-list-tile-sub-title class="text--primary">{{ project.subtitle }}</v-list-tile-sub-title>
            <v-list-tile-sub-title>{{ project.subtitle }}</v-list-tile-sub-title>
          </v-list-tile-content>
          <v-list-tile-action>
            <v-icon color="grey lighten-1" @click="$router.push({name: 'ProjectEdit', params:{project_id_prop:project.id}})">edit</v-icon>
          </v-list-tile-action>
        </v-list-tile>
        <v-divider></v-divider>
      </template>
    </v-list>
  </v-container>
</template>
<script>
  import postservice from '@/services/postservice'
  import authservice from '@/services/authservice'

  export default {
    data: function () {
      return {
        projects: []
      }
    },
    methods: {
      logout () {
        authservice.logout()
        this.$router.push({name: 'HomeView'})
      }
    },
    created () {
      postservice.getAllProjects().then((response) => {
        this.projects = response.data
      })
    }
  }
</script>



